export default {
	onShareAppMessage(){},
	data() {
		return {
			header_index: 0, // 默认为显示第一个
			header_data: ['正在上映', '即将上映'],
			AllData: []
		}
	},
	onLoad() {
		this.dp_index()
	},
	methods: {
		// 大盘接口
		async dp_index() {
			let userType  = await this.detection_validity()
			var init = this.header_index;
			var ini = 0;
			if (init == 0) {
				ini = 1
			} else {
				ini = 2
			}
			let {data}  = await this.$http.get({
				url:'/topprogram/get-release-movie',
				data:{
					type:ini,
					userType
				}
			})
			this.AllData = data;
		},
		// 跳转
		nagitto(mid, id) {
			uni.navigateTo({
				url: './detail/detail?id=' + id + '&mid=' + mid
			});
		},
		// 切换顶部tab
		header_click(index) {
			this.header_index = index;
			this.dp_index()
		}
	}
}
